body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.heading {
    color: #fff;
    font-size: 34px;
    font-weight: normal;
    padding: 20px 0;
    text-align: center;
    text-transform: uppercase;
    background: #6498fe;
  }
  ul{
    float: left;
    display: block;
    padding: 20px;

  }
  li{
    list-style-type: none;
    padding: 05px;
    margin-top: 10px;
  }
 
.message{
    border: 2px solid #6498fe;
    border-radius: 10px;
    background-color: #6498fe;
    color: whitesmoke;
    padding: 05px;

}

.name{
    align-items: initial;
}

.message-field {
    float: left;
    font-size:45px;
    display:block;
    width:98%;
    right: auto;
    left: auto;
    bottom: auto;
    padding:40px 10px 10px 5px;
    border: none;
    margin-top: 40px;;
}
.form-box{
    background: #fff;
    margin: 30px auto;
    max-width: 500px;
    background-color: whitesmoke;
}  

.head {
    color: #fff;
    font-size: 34px;
    font-weight: normal;
    padding: 30px 0;
    text-align: center;
    text-transform: uppercase;
    background: #6498fe;
  }

.form-group{
    padding: 20px;
    position: relative;
    width: 100%;
    overflow: hidden;
}

input[type="submit"] {
    background: #6498fe;
    border:none;
    border-radius: 2px;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 3px;
    margin: 5px 0;
    outline: medium none;
    overflow: hidden;
    padding: 10px;
    text-transform: uppercase;
    transition: all 0.15s ease-in-out 0s;
    width: 100%;
    box-shadow: 0 1px 2px 0px rgba(0,0,0,0.16), 0 1px 2px 0px rgba(0,0,0,0.23);
  }
  input[type="submit"]:hover {
    background: #4b81eb;
    box-shadow: 0 2px 4px 0px rgba(0,0,0,0.16), 0 2px 4px 0px rgba(0,0,0,0.23);
  }
  
  .form-box > input{
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:300px;
    border:none;
    border-bottom:1px solid #757575;
    right: auto;
    left: auto;
    margin-left: 80px;
  }
  .form-box > input:focus{ 
    outline:none; 
}
label 				 {
    color:#999; 
    font-size:18px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
  }
  
  /* active state */
  .form-box > input:focus ~ label, input:valid ~ label 		{
    top:-20px;
    font-size:14px;
    color:#5264AE;
  }
  
